html {
	background: #272727;
}

body {
	background: #272727;
}


.text {
	text-align: center;
	color: #fff;
	font-size: 15px;
	font-weight: 400;
	padding-left: 10px;
	padding-right: 10px;
	margin-top: 0px;
}

.phote {
	border-radius: 20px;
	width: 100px;
	margin: 10px auto;
}

.button {
	display: inline-block;
	font-size: 15px;
	background-color: #44709e;
	font-weight: 400;
	padding: 15px;
	text-decoration: none;
	color: #fff;
	width: 90%;
	border-radius: 10px;
	text-align: center;
	cursor: pointer;
	transition: 0.5s ease;
}

.button2 {
	display: inline-block;
	font-size: 15px;
	background-color: #cfd9e8;
	font-weight: 400;
	padding: 15px;
	text-decoration: none;
	color: #000;
	width: 90%;
	border-radius: 10px;
	text-align: center;
	cursor: pointer;
	transition: 0.5s ease;
	margin-top: 10px;
}

.button:hover, .button2:hover {
	transform: scale(0.98);
}

.grop {
	width: 95%;
	margin: 0 auto;
	padding-left: 10px;
	padding-right: 10px;
	background-color: gray;
	border-radius: 10px;
}

.ds {
	text-align: center;
}

.title {
	text-align: center;
	color: #007bff;
	font-size: 20px;
	font-weight: 500;
	padding-left: 10px;
	padding-right: 10px;
	margin-bottom: 5px;
}

.header {
	display: inline-block;
	width: 100%;
	background-color: #fff;
	padding-top: 15px;
	padding-bottom: 15px;
	border-bottom: 1px solid #ccc;
	text-transform: uppercase;
	font-weight: 600;
	font-size: 20px;
}

.sd {
	font-size: 18px;
	font-weight: normal;
	color: #fff;
}

.bof {
	background-color: #272727;
	background: #272727;
}